.button {
  width: 100%;
  min-height: 60px;
  padding-right: 20px;
  padding-left: 20px;
  font-weight: 900;
  font-size: 22px;
  line-height: 20px;
  font-family: "Nekst", "Arial", sans-serif;
  color: #ffffff;
  background: #ff9514;
  border: none;
  border-radius: 40px;
  cursor: pointer;
  transition: background 0.2s ease; }
  @media (min-width: 768px) {
