      background: #575757; }
    .button:disabled {
      background: #ff9514;
      opacity: 0.5; }
    .button.is-loading {
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: default; } }
  @media screen and (hover: hover), screen and (min-width: 0\0) {
    .button.is-loading:hover {
      background: #ff9514; } }
  @media screen {
